import React, {Component} from 'react';
import { CryptoCard } from '../../../../@jumbo/components/Common';
import BitcoinGraph from './BitcoinGraph';
import {Contracts, Formatter} from '../../../../utils/index';
import IntlMessages from '../../../../@jumbo/utils/IntlMessages';

class BitcoinPurchaseHistory extends Component {
  constructor() {
    super();
    this.state = { balance: 0 };
  }

  async componentDidMount() {
    await this.fund();

  }

  fund = async() => {
    let balance = await Contracts.getFundETH();
    if (balance) {
      this.setState({balance: Formatter.getRoundedBalance(balance.toString())});
    }
    return;
  }

  getNativeCoin = () => {
    if (window.localStorage.getItem('chain') == "ETH") {
      return ' ETH';
    } else {
      return ' BNB';
    }
  }

  render() {
    return (
      <div>
        <CryptoCard title={<IntlMessages id={'crypto.fundhistory'} />} amount={this.state.balance+ this.getNativeCoin()} progress={{ value: '0%', icon: 'info', color: '#8DCD03' }}>
          <BitcoinGraph />
        </CryptoCard>
      </div>
    );
  }
}





export default BitcoinPurchaseHistory;
