import React from 'react';
import { Area, AreaChart, ResponsiveContainer, Tooltip } from 'recharts';
import Box from '@material-ui/core/Box';
// import { crypto } from '../../../../@fake-db';
import {Contracts, Formatter} from '../../../../utils/index';
import { makeStyles } from '@material-ui/styles';
import { setGlobal, getGlobal, useEffect } from 'reactn';

const useStyles = makeStyles(theme => ({
  tooltip: {
    position: 'relative',
    borderRadius: 6,
    padding: '4px 12px',
    backgroundColor: '#8d46ef',
    color: theme.palette.common.white,
  },
}));

const crypto = {
  fund: []
}


function getChainCoin() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return 'ETH'
  } else {
    return 'BNB'
  }
}

const BitcoinGraph = () => {
  const classes = useStyles();
  const [graphData, setGraphData] = React.useState([]);
  let test = async () => {
    let ETHHistory = await Contracts.getFundHistory();
    for (let i = 0; i < ETHHistory[0].length; i++) {
      crypto.fund.push({transaction: i.toString(), price: Formatter.getRoundedBalance(ETHHistory[0][i])})
    }
  }

  useEffect(() => {
    async function loadData() {
      crypto.fund = [];
      await test();
      setGraphData(crypto.fund);
    }     

    

    loadData();
  }, []);

  return (
    <div>
        {graphData.length > 0 ?
          <ResponsiveContainer className={classes.root} width="100%" height={112}>
            <AreaChart data={graphData} margin={{ top: 0, right: 0, left: 0, bottom: 0 }}>
              <Tooltip
                labelStyle={{ color: 'black' }}
                cursor={false}
                content={data => {
                  return data.payload[0] ? <Box className={classes.tooltip}>{data.payload[0].payload.price} {getChainCoin()}</Box> : null;
                }}
              />
              <defs>
                <linearGradient id="color3" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#6200EE" stopOpacity={1} />
                  <stop offset="95%" stopColor="#B819D2" stopOpacity={1} />
                </linearGradient>
              </defs>
              <Area dataKey="price" strokeWidth={0} stackId="2" stroke="#6200EE" fill="url(#color3)" fillOpacity={1} />
            </AreaChart>
          </ResponsiveContainer>
          :
          null
        }
    </div>
  );
};

export default BitcoinGraph;
