import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { Box } from '@material-ui/core';
import IntlMessages from '../../../../@jumbo/utils/IntlMessages';

const useStyles = makeStyles(theme => ({
    root: {
      '& > *': {
        margin: theme.spacing(2),
      },
    },
  }));

function getFundLink() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return 'https://etherscan.io/address/0x2c9728ad35c1cfb16e3c1b5045bc9ba30f37fac5'
  } else {
    return 'https://bscscan.com/address/0x5D183417Ad08373e61fFfAC203bAbC44bA8fDf59'
  }
}

function getLinkText() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return <IntlMessages id={'crypto.viewcontract'} />
  } else {
    return <IntlMessages id={'crypto.viewcontractBEP'} />
  }
  
}

export default function EtherscanLink() {
    const classes = useStyles();
  
    return (
      <Box className={classes.root}>
        <Button target="_blank" rel="noopener noreferrer" variant="contained" color="primary" href={getFundLink()}>
         {getLinkText()}
        </Button>
      </Box>
    );
  }