import React, {Component} from 'react';
import { CryptoCard } from '../../../../@jumbo/components/Common';
// import BitcoinGraph from './BitcoinGraph';
import {Contracts, Formatter} from '../../../../utils/index';
import IntlMessages from '../../../../@jumbo/utils/IntlMessages';
import CmtCard from '../../../../@coremat/CmtCard';
import CmtCardHeader from '../../../../@coremat/CmtCard/CmtCardHeader';
import CmtCardContent from '../../../../@coremat/CmtCard/CmtCardContent';
import ActivitiesTimeLine from './ActivitiesTimeLine';
import { intranet } from '../../../../@fake-db';
import { timeFromNow } from '../../../../@jumbo/utils/dateHelper';
import PerfectScrollbar from 'react-perfect-scrollbar';
import makeStyles from '@material-ui/core/styles/makeStyles';

let userActivities = {
    activity: []
};
let number = 1;

// const useStyles = makeStyles(theme => ({
//   cardRoot: {
//     position: 'relative',
//   },
//   scrollbarRoot: {
//     height: 300,
//     marginRight: -24,
//     paddingRight: 24,
//     marginLeft: -24,
//     paddingLeft: 24,
//     marginTop: -5,
//     paddingTop: 5,
//   },
// }));
const classes = {
    cardRoot: {
        position: 'relative',
      },
      scrollbarRoot: {
        height: 300,
        marginRight: -24,
        paddingRight: 24,
        marginLeft: -24,
        paddingLeft: 24,
        marginTop: -5,
        paddingTop: 5,
      },
};

class RecentActivities extends Component {
  constructor() {
    super();
    this.state = { balance: 0, activity: [] };
  }

  async componentDidMount() {
    await this.fund();
    await this.test();
  }

  fund = async() => {
    let balance = await Contracts.getFundETH();
    if (balance) {
      this.setState({balance: Formatter.getRoundedBalance(balance.toString())});
    }
    return;
  }

  test = async () => {
    let ETHHistory = await Contracts.getFundHistory();

    userActivities.activity = [];
    for (let i = ETHHistory[0].length-1; i >= 0; i--) {
        userActivities.activity.push({
            id: i,
            date: '',
            title: ETHHistory[2][i],
            description: 'Linked address: ' + ETHHistory[1][i],
            logo: '/images/pentagon.png',
            color: '#17A9FC',
        })
    }
    this.setState({activity: userActivities.activity});
}


  

  render() {
    return (
      <div>
        <CmtCard className={classes.cardRoot}>
            <CmtCardHeader title="From newest to oldest" subTitle={`Accounts for all fund activity`} />
            <CmtCardContent>
                <PerfectScrollbar className={classes.scrollbarRoot}>
                    <ActivitiesTimeLine data={this.state.activity} />

                </PerfectScrollbar>
            </CmtCardContent>
        </CmtCard>
      </div>
    );
  }
}





export default RecentActivities;
